.contacts {
  background-color: $bgcolor-primary;
  @media screen and (max-width: 1199px) {
    padding: 40px 0px;  
    @include desktop {
      padding-bottom: 120px 0px;
    }
}
}
.contacts__list {
  @include tablet {
    display: flex;
    flex-wrap: wrap;
    margin: -16px;
  } 
  @include desktop {
    display: flex;
    flex-wrap: wrap; 
  }
}
.contacts__item {
  width: 280px;
  height: 412px;
  padding: 40px 32px 24px;
  background-color: $color-white-primary;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  margin: 0px auto;

  &:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 16px;
    }
  }
  @include tablet {
    width: 100%;
    height: 537px;
    padding: 40px 24px;
    flex-basis: calc((100% - 3 * 32px) / 3);
    margin: 16px;
  }
  @include desktop {
    width: 368px;
  height: 542px;
  padding: 40px;
  flex-basis: calc((100% - 3 * 32px) / 3);
    margin: 16px;
  }
}
.contacts__list,
.address__list,
.time-work__list,
.communication__link,
.contacts__modal {
  @extend %list;
}
.contacts-title {
  @include font(12, 500, 18, 0.04);
  @include centerdFlex;
  width: 80px;
  height: 22px;
  border-radius: 4px;
  background-color: $color-accent;
  color: $color-black-primary;
  margin-bottom: 20px;
  @include tablet {
  font-size: 16px;  
  width: 73px;
  height: 30px;
  margin-bottom: 32px;
  @include desktop {
    width: 76px;
    font-size: 16px;
    line-height: 1.37;
  }
  }
}
.cont-title {
  width: 130px;
  @include tablet {
    width: 138px;
  }
  @include desktop {
    width: 148px;
  }
}
.address__city {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  letter-spacing: 0.04em;
  color: $color-black-primary;
  margin-bottom: 8px;
  @include tablet {
    font-size: 14px; 
    margin-bottom: 12px; 
  }
  @include desktop {
    font-size: 16px;
    margin-bottom: 12px;
  }
}
.address__text {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.83;
  letter-spacing: 0.04em;
  color: $bgcolor-secondary;
  @include tablet {
    font-size: 14px;  
  }
  @include desktop {
    font-size: 16px;
    line-height: 1.81;
  }
}

.address__list::after,
.time-work__list::after {
  content: '';
  display: block;
  width: 100%;
  margin: 24px 0px;
  border: 1px solid $color-button-hover;
  @include tablet {
    margin: 32px 0px; 
  }
  @include desktop {
  margin: 32px 0px;
}
}
.time-work__card:not(:last-child) {
  margin-bottom: 8px;
  @include desktop{
    margin-bottom: 12px;
  }
}
.time-work__week {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.83;
  letter-spacing: 0.04em;
  color: $color-black-primary;
  @include tablet {
    font-size: 14px;  
  }
  @include desktop {
    font-size: 16px;
  }
}
.time-work__time {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.83;
  letter-spacing: 0.04em;
  color: $color-button-hover;
  @include tablet {
    font-size: 14px;  
  }
  @include desktop {
    font-size: 16px;
  }
}
.communication__phone,
.communication__mail,
.address__text {
  @extend %link;
}
.communication__phone {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: $color-black-primary;
  margin-bottom: 6px;
  @include tablet {
    font-size: 14px;  
    margin-bottom: 4px;
  }
  @include desktop {
    font-size: 16px;
    margin-bottom: 4px;
  }
}
.communication__mail {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: $color-black-primary;
  @include tablet {
    font-size: 14px;  
  }
  @include desktop {
    font-size: 16px;
  }
}

.contacts__modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  margin-top: 20px;
  @include tablet {
  display: flex;  
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  margin-top: 40px;  
  }
  @include desktop{
  margin-top: 60px; 
  }
}
.contacts__modal-item:first-child {
  @media screen and (max-width: 479px) {
    margin-bottom: 16px;
  }
}
.contacts__btn-loc {
  @extend %btn-primary;
  min-width: 196px;
  height: 42px;
  padding: 12px 39px 12px 40px;
  @include tablet {
    margin-right: 16px;
  }

&:hover {
  background-color: $color-button-hover;
}
&:focus {
  color: $color-white-primary;
}
}
.contacts__btn-fran {
 @extend %btn-secondary;
 min-width: 196px;
  height: 42px;
  padding: 12px 54px 12px 55px; 

  &:hover {
    border: $border-btn-secondary;
  }
  &:focus {
    color: $color-white-primary;
  }
}